<template>
  <div class="cart-bottom-tab">
    <div class="allselected" @click="selectedall">
      <img src="~@/assets/img/cart/tick.svg"  :class="{'selsecallCss':checkall}" alt="" />
      <span class="allselected-iten" >全选</span>
      <span class="allprice">合计:￥{{ allpriced }}</span>
    </div>
    <div class="computedposition">
      <span class="computed">结算({{ sunCount }})</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "CartBottomTab",
  data(){
    return{
      // checkall:this.selectedallTF
    }
  },
  methods:{
    selectedall(){
     if(this.checkall){
        this.$store.state.cartList.forEach(item => {
        item.selected = false
        this.$bus.$emit('selectall')
      });
     }else{
         this.$store.state.cartList.forEach(item => {
        item.selected = true
        this.$bus.$emit('noelectall')
        
      });
     }


  
    }
  },
  computed: {
    allpriced() {
      return this.$store.state.cartList
        .filter((item) => {
          return (item.selected );
        }).reduce((pre, item) => {
          return pre + item.count * item.price;
        }, 0)
        .toFixed(2);
    },
    sunCount() {
     return this.$store.state.cartList
        .filter((item) => {
          return (item.selected );
        }).reduce((pre, item) => {
          return pre + item.sunCount ;
        }, 0)
    },
    checkall(){
      if(this.$store.state.cartList.length===0){
        return false
      }
        return (this.$store.state.cartList.every((item) =>  item.selected ))
    }
  },
};
</script>

<style>
.cart-bottom-tab {
  /* position: relative; */
  position: absolute;
  display: flex;

  left: 0;
  right: 0;
  bottom: 49px;
  height: 46px;
  background-color: #eee;
  justify-content: center;
  flex-direction: column;

  /* align-items: center */
}
.allselected {
  padding: 12px 0 12px 12px;
}
.allselected img {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  border: 1px solid #ccc;
  vertical-align: bottom;
}
.allselected .allselected-iten {
  font-size: 14px;
  color: rgb(97, 93, 93);
  padding-left: 5px;
}
.allselected .allprice {
  margin-left: 4vw;
  font-size: 17px;
}
.computedposition {
  position: relative;
}
.computed {
  position: absolute;
  /* float: right; */
  /* display: inline-block; */
  width: 100px;
  height: 44px;
  background-color: orangered;
  color: azure;
  text-align: center;
  right: 0;
  top: -43px;
  line-height: 44px;
}
.selsecallCss{
  background-color: #ff8198;
}
</style>