<template>
  <div class="cart-item">
    <div class="cart-choose">
      <img
        src="~@/assets/img/cart/tick.svg"
        alt=""  @click="imageClick"
        :class="{ imageselected: imageselectedtf }"
      />
    </div>
    <div class="cart-item-data">
      <img :src="cartitemdata.image" alt="" />
    </div>
    <div class="cart-item-name">
      <div class="cart-title">{{ cartitemdata.title }}</div>
      <div class="cart-desc">{{ cartitemdata.desc }}</div>
      <div class="counntAndprice">
        <div class="cart-price">￥{{ cartitemdata.price }}</div>
        <div class="cart-count">×{{ cartitemdata.count }}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "CartItem",
  data(){
    return{
     imageselectedtf:false
    }
  },
  methods: {
    imageClick(){
      this.cartitemdata.selected=!this.cartitemdata.selected
      this.imageselectedtf=this.cartitemdata.selected
    }},
    activated(){
      this.$bus.$on('selectall',()=>{
        this.imageselectedtf=false
      })
       this.$bus.$on('noelectall',()=>{
      this.imageselectedtf= true
      })
    },
  props: {
    cartitemdata: [],
  },
};
</script>

<style scoped>
.cart-item {
  display: flex;
  width: 100vw;
  /* height: 120px; */
  padding: 5px;
  /* background-color: rgb(241, 237, 233); */
  border-bottom: 1px solid #ccc;
}
.cart-choose {
  width: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  /* background-color: rgb(219, 233, 18); */
}
.cart-choose img {
  border-radius: 50%;
}
.cart-choose .imageselected {
  background-color: #ff8198;
}
.cart-item-data {
  padding: 5px;
}
.cart-item-data img {
  width: 80px;
  height: 100px;
  display: block;
  border-radius: 5px;
  /* background-color: rgb(39, 18, 233); */
}
.cart-item-name {
  font-size: 17px;
  color: #333;
  padding: 5px 10px;
  position: relative;
  overflow: hidden;
  /* background-color: rgb(233, 18, 169); */
}
.cart-title,
.cart-desc {
  overflow: hidden; /*超出部分隐藏*/
  white-space: nowrap; /*不换行*/
  text-overflow: ellipsis; /*超出部分省略号显示*/
}
.cart-item-name .cart-desc {
  font-size: 14px;
  color: #666;
  margin-top: 15px;
}
.counntAndprice {
  margin-top: 10px;
  position: absolute;
  bottom: 10px;
  left: 10px;
  right: 10px;
  /* display: block */
}
.counntAndprice .cart-price {
  /* pad-top:10px; */
  /* padding-top: 10px; */
  color: orangered;
  float: left;
}
.counntAndprice .cart-count {
  float: right;
}
</style>